import React, { useState, useEffect } from 'react';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Badge } from '@/components/ui/badge';
import { Coins, Plus, AlertTriangle, Infinity, RefreshCw, BarChart3 } from 'lucide-react';
import { toast } from 'sonner';
import { useNavigate } from 'react-router-dom';
import axiosInstance from '@/api/axios/axiosInstance'; // Fixed import path
import PurchaseCreditsModal from './PurchaseCreditsModal';
import CreditUsageModal from './CreditUsageModal';
import secureLocalStorage from 'react-secure-storage';

const CreditBalance = ({ showPurchaseButton = true, compact = false, showUsageButton = false }) => {
  const navigate = useNavigate();
  const [balance, setBalance] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [showPurchaseModal, setShowPurchaseModal] = useState(false);
  const [showUsageModal, setShowUsageModal] = useState(false);

  const fetchBalance = async () => {
    try {
      setLoading(true);
      console.log('ðŸ” CreditBalance: Fetching balance...');

      // Add cache-busting parameter to force fresh data
      const response = await axiosInstance.get(`/api/credits/balance?t=${Date.now()}`);
      console.log('ðŸ” CreditBalance: API response:', response.data);

      const actualBalance = response.data.data;
      console.log('ðŸ” CreditBalance: Raw API response:', actualBalance);

      setBalance(actualBalance);
      setError(null);

      console.log('âœ… CreditBalance: Balance updated:', actualBalance);
      console.log('ðŸŽ¯ CreditBalance: Should show:', actualBalance.available_credits, 'credits');
    } catch (err) {
      console.error('âŒ CreditBalance: Error fetching credit balance:', err);
      console.error('âŒ CreditBalance: Error response:', err.response?.data);
      setError(err.response?.data?.message || 'Failed to fetch credit balance');
      toast.error('Failed to fetch credit balance');
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    console.log('ðŸ” CreditBalance: Component mounted, fetching balance...');
    fetchBalance();

    // Listen for credit balance updates
    const handleCreditUpdate = () => {
      console.log('ðŸ” CreditBalance: Credit update event received, refreshing...');
      fetchBalance();
    };

    window.addEventListener('creditBalanceUpdate', handleCreditUpdate);

    // Also listen for storage changes (in case token changes)
    const handleStorageChange = () => {
      console.log('ðŸ” CreditBalance: Storage change detected, refreshing...');
      setTimeout(fetchBalance, 100); // Small delay to ensure token is updated
    };

    window.addEventListener('storage', handleStorageChange);

    return () => {
      window.removeEventListener('creditBalanceUpdate', handleCreditUpdate);
      window.removeEventListener('storage', handleStorageChange);
    };
  }, []);

  const handlePurchaseCredits = () => {
    console.log('ðŸ” Buy button clicked - opening purchase modal');
    setShowPurchaseModal(true);
  };

  const handlePurchaseSuccess = (paymentIntent) => {
    toast.success('ðŸŽ‰ Purchase successful! Credits added to your account.');
    setShowPurchaseModal(false);
    fetchBalance(); // Refresh balance

    // Trigger global credit balance update
    window.dispatchEvent(new CustomEvent('creditBalanceUpdate'));
  };



  const formatCredits = (credits) => {
    console.log('ðŸ” formatCredits called with:', credits, 'type:', typeof credits);

    if (credits === 'unlimited') return 'Unlimited';

    // Convert string to number if needed
    const numCredits = typeof credits === 'string' ? parseFloat(credits) : credits;
    console.log('ðŸ” numCredits after conversion:', numCredits, 'type:', typeof numCredits);

    if (typeof numCredits === 'number' && !isNaN(numCredits)) {
      const formatted = numCredits.toLocaleString('en-US', {
        minimumFractionDigits: 0,
        maximumFractionDigits: 2
      });
      console.log('ðŸ” formatted result:', formatted);
      return formatted;
    }
    console.log('ðŸ” returning default "0"');
    return '0';
  };

  const getBalanceColor = (availableCredits) => {
    if (availableCredits === 'unlimited') return 'text-green-600';

    const numCredits = typeof availableCredits === 'string' ? parseFloat(availableCredits) : availableCredits;
    if (numCredits <= 10) return 'text-red-600';
    if (numCredits <= 50) return 'text-yellow-600';
    return 'text-green-600';
  };

  const getBalanceBadgeVariant = (availableCredits) => {
    if (availableCredits === 'unlimited') return 'default';

    const numCredits = typeof availableCredits === 'string' ? parseFloat(availableCredits) : availableCredits;
    if (numCredits <= 10) return 'destructive';
    if (numCredits <= 50) return 'secondary';
    return 'default';
  };

  if (loading) {
    return (
      <Card className={compact ? 'w-full' : 'w-full max-w-md'}>
        <CardContent className="p-4">
          <div className="flex items-center space-x-2">
            <Coins className="h-5 w-5 text-gray-400 animate-pulse" />
            <div className="h-4 bg-gray-200 rounded w-20 animate-pulse"></div>
          </div>
        </CardContent>
      </Card>
    );
  }

  if (error) {
    return (
      <Card className={compact ? 'w-full' : 'w-full max-w-md'}>
        <CardContent className="p-4">
          <div className="flex items-center space-x-2 text-red-600">
            <AlertTriangle className="h-5 w-5" />
            <span className="text-sm">Error loading balance</span>
          </div>
        </CardContent>
      </Card>
    );
  }

  const compactView = (
    <div className="flex items-center space-x-2">
      <Coins className="h-4 w-4 text-gray-600" />
      <span className={`font-medium text-sm ${getBalanceColor(balance?.available_credits)}`}>
        {balance?.isSuperAdmin ? (
          <div className="flex items-center space-x-1">
            <Infinity className="h-4 w-4" />
            <span>Unlimited</span>
          </div>
        ) : (
          <span title={`${formatCredits(balance?.available_credits)} credits available`}>
            {formatCredits(balance?.available_credits)} credits
          </span>
        )}
      </span>

      {/* Show low credit warning */}
      {!balance?.isSuperAdmin && parseFloat(balance?.available_credits) <= 10 && parseFloat(balance?.available_credits) > 0 && (
        <AlertTriangle className="h-4 w-4 text-yellow-500" title="Low credit balance" />
      )}

      {/* Show zero credit warning */}
      {!balance?.isSuperAdmin && parseFloat(balance?.available_credits) <= 0 && (
        <AlertTriangle className="h-4 w-4 text-red-500" title="No credits remaining" />
      )}

      {showUsageButton && (
        <Button
          size="sm"
          variant="outline"
          onClick={() => setShowUsageModal(true)}
          className="h-7 px-3 text-xs"
        >
          <BarChart3 className="h-3 w-3 mr-1" />
          Credits Usage
        </Button>
      )}

      {showPurchaseButton && !balance?.isSuperAdmin && (
        <Button
          size="sm"
          variant={parseFloat(balance?.available_credits) <= 10 ? "default" : "outline"}
          onClick={handlePurchaseCredits}
          className="h-7 px-3 text-xs"
        >
          <Plus className="h-3 w-3 mr-1" />
          Buy Credits
        </Button>
      )}
    </div>
  );

  const fullView = (
    <Card className="w-full max-w-md">
      <CardHeader className="pb-3">
        <CardTitle className="flex items-center space-x-2 text-lg">
          <Coins className="h-5 w-5" />
          <span>Credit Balance</span>
        </CardTitle>
      </CardHeader>
      <CardContent className="space-y-4">
        {balance?.isSuperAdmin ? (
          <div className="text-center">
            <div className="flex items-center justify-center space-x-2 mb-2">
              <Infinity className="h-8 w-8 text-green-600" />
              <span className="text-2xl font-bold text-green-600">Unlimited</span>
            </div>
            <Badge variant="default" className="bg-green-100 text-green-800">
              Super Admin
            </Badge>
          </div>
        ) : (
          <>
            <div className="text-center">
              <div className={`text-3xl font-bold ${getBalanceColor(balance?.available_credits)}`}>
                {formatCredits(balance?.available_credits)}
              </div>
              <p className="text-sm text-gray-600 mt-1">Available Credits</p>

              {parseFloat(balance?.available_credits) <= 10 && (
                <Badge variant={getBalanceBadgeVariant(balance?.available_credits)} className="mt-2">
                  <AlertTriangle className="h-3 w-3 mr-1" />
                  Low Balance
                </Badge>
              )}
            </div>

            <div className="grid grid-cols-2 gap-4 text-sm">
              <div className="text-center">
                <div className="font-medium text-gray-900">
                  {formatCredits(balance?.total_credits)}
                </div>
                <div className="text-gray-600">Total</div>
              </div>
              <div className="text-center">
                <div className="font-medium text-gray-900">
                  {formatCredits(balance?.used_credits)}
                </div>
                <div className="text-gray-600">Used</div>
              </div>
            </div>

            {balance?.last_purchase_at && (
              <div className="text-xs text-gray-500 text-center">
                Last purchase: {new Date(balance.last_purchase_at).toLocaleDateString()}
              </div>
            )}

            {showPurchaseButton && (
              <Button
                onClick={handlePurchaseCredits}
                className="w-full"
                variant={parseFloat(balance?.available_credits) <= 10 ? "default" : "outline"}
              >
                <Plus className="h-4 w-4 mr-2" />
                Purchase Credits
              </Button>
            )}
          </>
        )}

        <div className="flex justify-between text-xs text-gray-500">
          <button
            onClick={fetchBalance}
            className="hover:text-gray-700 transition-colors"
          >
            Refresh
          </button>
          {/* <a
            href="/credits/history"
            className="hover:text-gray-700 transition-colors"
          >
            View History
          </a> */}
        </div>
      </CardContent>
    </Card>
  );

  return (
    <>
      {compact ? compactView : fullView}

      {/* Purchase Credits Modal */}
      <PurchaseCreditsModal
        isOpen={showPurchaseModal}
        onClose={() => setShowPurchaseModal(false)}
        onSuccess={handlePurchaseSuccess}
      />

      {/* Credit Usage Modal */}
      <CreditUsageModal
        isOpen={showUsageModal}
        onClose={() => setShowUsageModal(false)}
      />
    </>
  );
};

export default CreditBalance;
